<template>
  <div id="app">
    <v-header :seller="seller" ></v-header>
    <div class="tab-wp">
      <tab :tabs="tabs" :initialIndex=0></tab>
    </div>
  </div>
</template>

<script>
  import VHeader from './components/v-header/v-header'
  import Seller from './components/seller/seller'
  import Goods from './components/goods/goods'
  import Ratings from './components/ratings/ratings'
  import Tab from './components/tab/tab'
  import { getSeller } from 'api'
  import qs from 'query-string'

  export default {
    name: 'app',
    data () {
      return {
        seller: {
          id: qs.parse(location.search).id
        }
      }
    },
    created () {
      this._getSeller()
    },
    methods: {
      _getSeller () {
        getSeller({
          id: this.seller.id
        }).then((seller) => {
          this.seller = seller
        })
      }
    },
    computed: {
      tabs () {
        return [
          {
            label: '',
            component: Goods,
            data: {
              seller: this.seller
            }
          },
          {
            label: '',
            component: Ratings,
            data: {
              seller: this.seller
            }
          },
          {
            label: '',
            component: Seller,
            data: {
              seller: this.seller
            }
          }
        ]
      }
    },
    components: {
      VHeader,
      Tab
    }
  }
</script>

<style lang="stylus" scoped>
  #app
    .tab-wp
      position: fixed
      top: 136px
      left: 0
      right: 0
      bottom: 0
</style>
