<template>
  <div class="star" :class="starType">
    <span class="star-item" v-for="(itemClass, index) in itemClasses" :class="itemClass" :key="index"></span>
  </div>
</template>

<script>
  const LENGTH = 5
  const CLS_ON = 'on'
  const CLS_OFF = 'off'
  const CLS_HALF = 'half'

  export default {
    name:'',
    props:{
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      starType () {
        return `star-${this.size}`
      },
      itemClasses () {
        let itemArr = []
        const score = Math.floor(this.score * 2) / 2
        const hasDecimal = score % 1 !== 0
        const integer = Math.floor(score)
        for (let i = 0; i < integer; i++) {
          itemArr.push(CLS_ON)
        }
        if (hasDecimal) {
          itemArr.push(CLS_HALF)
        }
        while (itemArr.length < LENGTH) {
          itemArr.push(CLS_OFF)
        }
        return itemArr
      }
    }
  }
</script>

